/* all */
$properties: (
    'm': 'margin',
    'p': 'padding'
);

$medias: (
    'sm': '576px',
    'md': '768px',
    'lg': '992px',
    'xl': '1200px'
);

//Ð•ÑÐ»Ð¸ Ð¾Ð±Ñ‹Ñ‡Ð½Ñ‹Ð¼ ÑÐ¿Ð¸ÑÐºÐ¾Ð¼ Ñ‚Ð¾ Ð² Ñ†Ð¸ÐºÐ»Ðµ Ð½ÐµÐ¾Ð±Ñ…Ð¾Ð´Ð¸Ð¼Ð¾ Ð¿Ð¸ÑÐ°Ñ‚ÑŒ ÑƒÑÐ»Ð¾Ð²Ð¸ÑŽ $value < 10 Ð¸ Ð² ÐºÐ»Ð°ÑÑÐµ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ 0 Ð¿ÐµÑ€ÐµÐ´ $value
$values: (
    '0': 0px,
    '05': 5px,
    '10': 10px,
    '15': 15px,
    '20': 20px,
    '30': 30px,
    '40': 40px,
    '50': 50px,
    '60': 60px,
    '100': 100px,
    'auto': auto
);

@each $key, $value in $values {
    @each $name, $glyph in $properties {
        /* all */
        .#{$name}-#{$key} {
            #{$glyph}-left: $value;
            #{$glyph}-right: $value;
            #{$glyph}-top: $value;
            #{$glyph}-bottom: $value;
        }
        /* endof all */

        /* top+bottom */

        .#{$name}y-#{$key} {
            #{$glyph}-top: $value;
            #{$glyph}-bottom: $value;
        }
        /* endof top+bottom */

        /* left and right*/

        .#{$name}x-#{$key} {
            #{$glyph}-left: $value;
            #{$glyph}-right: $value;
        }
        /* endof left and right*/

        /* top */

        .#{$name}t-#{$key} {
            #{$glyph}-top: $value;
        }
        /* endof top */

        /* bottom */

        .#{$name}b-#{$key} {
            #{$glyph}-bottom: $value;
        }

        /* endof bottom */

        /* left */

        .#{$name}l-#{$key} {
            #{$glyph}-left: $value;
        }
        /* endof left */

        /* right*/

        .#{$name}r-#{$key} {
            #{$glyph}-right: $value;
        }
    }
}

@each $name, $glyph in $properties {
    @each $mediaKey, $mediaSize in $medias {
        @each $key, $value in $values {
            @media only screen and (min-width: $mediaSize) {
                /* all */
                .#{$name}-#{$mediaKey}-#{$key} {
                    #{$glyph}-left: $value;
                    #{$glyph}-right: $value;
                    #{$glyph}-top: $value;
                    #{$glyph}-bottom: $value;
                }
                /* endof all */

                /* top+bottom */

                .#{$name}y-#{$mediaKey}-#{$key} {
                    #{$glyph}-top: $value;
                    #{$glyph}-bottom: $value;
                }
                /* endof top+bottom */

                /* left and right*/

                .#{$name}x-#{$mediaKey}-#{$key} {
                    #{$glyph}-left: $value;
                    #{$glyph}-right: $value;
                }
                /* endof left and right*/

                /* top */

                .#{$name}t-#{$mediaKey}-#{$key} {
                    #{$glyph}-top: $value;
                }
                /* endof top */

                /* bottom */

                .#{$name}b-#{$mediaKey}-#{$key} {
                    #{$glyph}-bottom: $value;
                }
                /* endof bottom */

                /* left */

                .#{$name}l-#{$mediaKey}-#{$key} {
                    #{$glyph}-left: $value;
                }
                /* endof left */

                /* right*/

                .#{$name}r-#{$mediaKey}-#{$key} {
                    #{$glyph}-right: $value;
                }
                /* endof right*/
            }
        }
    }
}
